import React from 'react';
import createItemImg from '../assets/create-items.gif';
import accessCodeImg from '../assets/accessing list code.gif';
import newMembersImg from '../assets/adding new members.gif';

const About = () => {
	return (
		<>
			<div className="about">
				<div className="blurredPhoto"></div>
				<div className="about-text">
					<h1>Create private or shared household shopping lists</h1>
					<h4>Promoting altruism in the household</h4>
					{sessionStorage.getItem('username') !== null && 
					<a href="Main"><b>Go to Your Lists</b></a>}
				</div>
			</div>
			<div className="about-body">
				<hr />
				<div className="dark-mode">
				<h1>Create a list and add items with or without an estimated price</h1>
				<img src={createItemImg}></img>
				
				<hr />
				<h1>Give household members your list name and list code</h1>
				<img src={accessCodeImg}></img>
				<hr />
				<h1>Your household members can add themselves to your list</h1>
				<img src={newMembersImg}></img>
				<h1>Only you can remove your items, but members in your list can complete any item for you</h1>
				{sessionStorage.getItem('username') !== null &&
				 <a href="Main"><b>Go to Your Lists</b></a>}
				</div>
			</div>
		</>
	);
};

export default About;
