import React, { useState, useEffect } from 'react';
import { Dropdown } from 'react-bootstrap';
import SelectableContext from 'react-bootstrap/SelectableContext';
import Sidebar from '../components/Sidebar';

const Main = () => {
	const [message, setMessage] = useState(' ');
	const username = sessionStorage.getItem('username');

	const [listname, setListname] = useState('');

	const [lists, setLists] = useState([]);
	const [listCode, setListCode] = useState('Create a List First');

	let defaultList = 'You have no lists.';
	const [currentList, setCurrentList] = useState(defaultList);

	const [item, setItem] = useState('');
	const [price, setPrice] = useState('');
	const [listItems, setListItems] = useState([]);

	const fetchLists = async () => {
		const result = await fetch(`/api/lists-per-person/${username}`);
		setLists(await result.json());
	};

	const createList = async () => {
		const result = await fetch(`/api/create-list/`, {
			method: 'post',
			body: JSON.stringify({ listname, username }),
			headers: {
				'Content-Type': 'application/json',
			},
		});
		const response = await result.text();
		if (response === 'true') {
			setListname('');
			fetchLists();
		} else setMessage(response);
	};

	const fetchListCode = async () => {
		if (currentList !== defaultList) {
			const result = await fetch(`/api/list-code/${currentList}`);
			setListCode(await result.text());
		}
	};

	const addItem = async () => {
		if (item !== '') {
			const result = await fetch(`/api/add-item`, {
				method: 'post',
				body: JSON.stringify({ username, currentList, item, price }),
				headers: {
					'Content-Type': 'application/json',
				},
			});
			const response = await result.text();
			console.log(JSON.stringify({ username, currentList, item, price }));
			console.log(response);

			if (response === 'true') {
				setItem('');
				setPrice('');
				getItems('');
			} else {
				setMessage(response);
			}
		}
	};

	const getItems = async () => {
		if (currentList !== '' || currentList !== defaultList) {
			const result = await (await fetch(`/api/items-per-list/${currentList}`)).json();
			setListItems(result);
		}
	};

	const deleteItem = async (item, price) => {
		const result = await fetch(`/api/delete-item`, {
			method: 'post',
			body: JSON.stringify({ username, currentList, item, price }),
			headers: {
				'Content-Type': 'application/json',
			},
		});

		getItems();
	};

	const completeItem = async (item, price) => {
		const result = await fetch(`/api/complete-item`, {
			method: 'post',
			body: JSON.stringify({ username, currentList, item, price }),
			headers: {
				'Content-Type': 'application/json',
			},
		});

		getItems();
	};

	useEffect(() => {
		fetchLists();
	}, [username]);

	useEffect(() => {
		fetchListCode();
		getItems();
	}, [currentList]);

	useEffect(() => {
		if (lists.length > 0) setCurrentList(lists[0]);
	}, [lists]);

	if (username === null) {
		return (
			<div>
				<h1>Please Login!</h1>
			</div>
		);
	} else {
		return (
			<>
				<div className="flexbox">
					<div className="latest-single">
						<div className="list-header">
							<h1>{currentList}</h1>
							{
							(lists.length > 0) && (<SelectableContext.Provider value={false}>
								<Dropdown>
									<Dropdown.Toggle className="members-btn">
										<a>Add Members</a>
									</Dropdown.Toggle>
									<Dropdown.Menu className="menu-spacing">
										<label>Give new members this list password:</label>
										<h1>{listCode}</h1>
									</Dropdown.Menu>
								</Dropdown>
							</SelectableContext.Provider>)
							}
						</div>

						<table className="table">
							<thead className="thead-light">
								<tr>
									<th scope="col"></th>
									<th scope="col">Name</th>
									<th scope="col">Item</th>
									<th scope="col">Price</th>
									<th scope="col">Status</th>
									<th scope="col"></th>
								</tr>
							</thead>
							<tbody>
								{listItems.length > 0 &&
									listItems.map((row, k) => (
										<>
											{row.items.map((entry, key) => (
												<tr>
													<th scope="row"></th>
													<td>{row.username}</td>
													<td>{entry.item}</td>
													<td>{entry.price > 0 && <p>${entry.price}</p>} </td>
													<td>
														{entry.completed === true ? (
															<p>Done</p>
														) : (
															<button
																className="orange-btn btn"
																value={entry.price}
																onClick={() => completeItem(entry.item, entry.price)}
															>
																<span>Incomplete</span>
															</button>
														)}
													</td>

													{row.username === username ? (
														<td>
															<a
																href="#"
																onClick={() => deleteItem(entry.item, entry.price)}
															>
																X
															</a>
														</td>
													) : (
														<td></td>
													)}
												</tr>
											))}
										</>
									))}
							</tbody>
						</table>
						<div className="item-form">
							<hr />
							<div className="wrapper">
								<label className="item-form-element">Item</label>
								<span className="item-form-element" id="item-msg"></span>
								<input
									className="item-form-element"
									type="text"
									value={item}
									onChange={(event) => setItem(event.target.value)}
								/>
								<label className="item-form-element">Price</label>
								<span className="item-form-element" id="price-msg"></span>
								<input
									className="item-form-element"
									type="number"
									min="0"
									value={price}
									onChange={(event) => setPrice(event.target.value)}
								/>
								<button
									className="item-form-element btn btn-dark add-item-btn"
									onClick={() => addItem()}
								>
									Add Item
								</button>
							</div>
						</div>
					</div>
					<Sidebar
						lists={lists}
						setCurrentList={setCurrentList}
						username={username}
						fetchLists={fetchLists}
						createList={createList}
						listname={listname}
						setListname={setListname}
					></Sidebar>
				</div>
			</>
		);
	}
};

export default Main;
