import React, { useState } from 'react';
import { Dropdown } from 'react-bootstrap';
import SelectableContext from 'react-bootstrap/SelectableContext';

const LoginForm = () => {
	const [username, setUsername] = useState('');
	const [password, setPassword] = useState('');

	const login = async () => {
		if (username.trim().length <= 0) document.getElementById('name-msg-login').innerText = 'Enter Username.';
		else {
			const result = await fetch(`/api/login/`, {
				method: 'post',
				body: JSON.stringify({ username, password }),
				headers: {
					'Content-Type': 'application/json',
				},
			});
			const response = await result.text();

			if (response !== 'true') document.getElementById('name-msg-login').innerText = response;
			else {
				window.sessionStorage.setItem('username', username);
				window.location.replace('/main');
			}
			setUsername('');
			setPassword('');
		}
	};

	function clearErrorMsg() {
		document.getElementById('name-msg-login').innerText = '';
		document.getElementById('password-msg-login').innerText = '';
	}

	return (
		<SelectableContext.Provider value={false}>
			<Dropdown>
				<Dropdown.Toggle className="menu-header teal">
					<a>Login</a>
				</Dropdown.Toggle>
				<Dropdown.Menu className="menu-spacing login ">
					<span id="name-msg-login" className="error"></span>
					<input
						type="text"
						value={username}
						placeholder="Username"
						onFocus={clearErrorMsg}
						onChange={(event) => setUsername(event.target.value)}
						autoFocus
						required
					/>
					<br />
					<span id="password-msg-login" className="error"></span>
					<input
						type="password"
						value={password}
						placeholder="Password"
						onFocus={clearErrorMsg}
						onChange={(event) => setPassword(event.target.value)}
						required
					/>
					<br />
					<input
						type="submit"
						className="btn btn-primary btn-block teal"
						id="loginButton"
						value="Login"
						onClick={() => login()}
					/>
				</Dropdown.Menu>
			</Dropdown>
		</SelectableContext.Provider>
	);
};

export default LoginForm;
