import React, { useState, useEffect } from 'react';
import { Dropdown } from 'react-bootstrap';
import SelectableContext from 'react-bootstrap/SelectableContext';

const Sidebar = ({lists, setCurrentList, username, fetchLists , createList, listname, setListname}) => {
	const [message, setMessage] = useState(' ');	
	const [enteredCode, setEnteredCode] = useState('');
	const [tempname, setTempname] = useState('');


	const addListByCode = async () => {
		if (enteredCode !== '' || tempname !== '') {
			const result = await fetch(`/api/add-member`, {
				method: 'post',
				body: JSON.stringify({ username, tempname, enteredCode }),
				headers: {
					'Content-Type': 'application/json',
				},
			});

			const response = await result.text();
			console.log(JSON.stringify({ username, tempname, enteredCode }));
			if (response === 'true') {
				setTempname('');
				setEnteredCode('');
				fetchLists();
			}
		}
	};

	return (
		<div className="sidebar">
			<h2>Other Lists</h2>
			<ul>
				{lists.length > 0 && (lists.map((list, key) => (
					<li key={key} onClick={() => setCurrentList(list)}>
						{list}
					</li>
				)))}
			</ul>

			<div className="sidebar-opts">
				<SelectableContext.Provider value={false}>
					<Dropdown>
						<Dropdown.Toggle className="create-list">
							<a>Create New List</a>
						</Dropdown.Toggle>
						<Dropdown.Menu className="menu-spacing">
							<span id="list-name-msg" className="error">
								{message}
							</span>
							<input
								type="text"
								id="name-entered"
								placeholder="Enter New List Name"
								value={listname}
								onChange={(event) => setListname(event.target.value)}
								onFocus={() => setMessage('')}
							/>
							<br/>
							<input className="orange-btn-menu  wide-btn btn" type="submit" value="Go" onClick={() => createList()} />
						</Dropdown.Menu>
					</Dropdown>
				</SelectableContext.Provider>

				<SelectableContext.Provider value={false}>
					<Dropdown>
						<Dropdown.Toggle className="list-by-code">
							<a onClick={() => setMessage('')} >Add List By Code</a>
						</Dropdown.Toggle>
						<Dropdown.Menu className="menu-spacing">
							<span id="list-name-msg" className="error">
								{message}
							</span>
							<input
								type="text"
								id="list-entered"
								placeholder="Enter List Name"
								onFocus={() => setMessage('')}
								value={tempname}
								onChange={(event) => setTempname(event.target.value)}
							/>
							<br />
							<span id="list-code-msg" className="error"></span>
							<input
								type="text"
								id="code-entered"
								placeholder="Enter List Password"
								value={enteredCode}
								onChange={(event) => setEnteredCode(event.target.value)}
							/>
							<br />
							<input type="submit" className="orange-btn-menu  wide-btn btn" value="Go" onClick={() => addListByCode()} />
						</Dropdown.Menu>
					</Dropdown>
				</SelectableContext.Provider>
			</div>
		</div>
	);
};

export default Sidebar;
