import React, { useState } from 'react';
import { Dropdown } from 'react-bootstrap';
import SelectableContext from 'react-bootstrap/SelectableContext';

const SignUpForm = () => {
	const [username, setUsername] = useState('');
	const [password, setPassword] = useState('');
	const [email, setEmail] = useState('');

	const signUp = async () => {
		if (username.trim().length <= 0 || email.trim().length <= 0 || !email.match('\\w+\\@\\w+\\.\\w+'))
			document.getElementById('name-msg').innerText = 'Missing Fields.';
		else if (password.length < 8)
			document.getElementById('name-msg').innerText = 'Passwords are 8 characters or more.';
		else {
			const result = await fetch(`/api/sign-up/`, {
				method: 'post',
				body: JSON.stringify({ username, email, password }),
				headers: {
					'Content-Type': 'application/json',
				},
			});
			const response = await result.json();
			console.log(JSON.stringify({ username, email, password }));
			if (response.newUser === false)
				document.getElementById('name-msg').innerText = 'This username already exists.';
			if (response.newEmail === false)
				document.getElementById('email-msg').innerText = 'This email already exists.';
			else {
				window.sessionStorage.setItem('username', username);
				window.location.replace('/main');
			}

			setUsername('');
			setPassword('');
			setEmail('');
		}
	};

	const clearErrorMsg = () => {
		document.getElementById('name-msg').innerText = '';
		document.getElementById('email-msg').innerText = '';
		document.getElementById('password-msg').innerText = '';
	};

	return (
		<SelectableContext.Provider value={false}>
			<Dropdown>
				<Dropdown.Toggle className="menu-header teal">
					<a>Create an Account</a>
				</Dropdown.Toggle>
				<Dropdown.Menu className="menu-spacing signup">
					<span id="name-msg" className="error"></span>
					<input
						type="text"
						placeholder="Username"
						value={username}
						onFocus={clearErrorMsg}
						onChange={(event) => setUsername(event.target.value)}
						autoFocus
						required
					/>
					<br />
					<span id="email-msg" className="error"></span>
					<input
						type="email"
						placeholder="Email"
						value={email}
						onFocus={clearErrorMsg}
						onChange={(event) => setEmail(event.target.value)}
						required
					/>
					<br />
					<span id="password-msg" className="error"></span>
					<input
						type="password"
						placeholder="Password"
						value={password}
						onChange={(event) => setPassword(event.target.value)}
						onFocus={clearErrorMsg}
						required
					/>
					<br />
					<input
						type="submit"
						className="btn btn-primary menu-button teal"
						value="Sign Up"
						onClick={() => signUp()}
					/>
				</Dropdown.Menu>
			</Dropdown>
		</SelectableContext.Provider>
	);
};

export default SignUpForm;
